* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list__item {
  display: flex;
  align-items: center;
  margin-bottom: 5px;
}

.country-list__flag {
  margin-right: 5px;
}

.country-list {
  padding: 0;
}

.country-info {
  display: flex;
  flex-direction: column;
  /* align-items: center; */
}

.country-info__title {
  margin-left: 10px;
}

.country-info__logo {
  display: flex;
  align-items: center;
}

.country-info__list {
  list-style: none;
  padding: 0;
}

.country-info__item {
  display: flex;
  align-items: center;
}

.country-info__item-title {
  margin: 0 5px 5px 0;
}